import { BiPluginConfig } from '../../types';
import { cardFactory, CustomizedBiCardPluginType } from '@ali/4ever-factory';
import locale from '../../locales/zh_CN';
import createOnCangjiePaste from './handlers/createOnCangjiePaste';
import createRenderNode from './createRenderNode';
import createMenubar from './handlers/createMenu';

const createPlugin = (bizConfig: BiPluginConfig): CustomizedBiCardPluginType => {
  const calendarCardConfig = bizConfig.calendarCard || {
    locale: { ...locale.calendarCard.locale },
  };
  const renderNode: CustomizedBiCardPluginType['renderNode'] = createRenderNode(calendarCardConfig);
  return {
    cardType: 'calendar',
    cardStyle: {
      minWidth: 300,
    },
    renderNode,
    onCangjiePaste: createOnCangjiePaste(bizConfig),
    menubar: {
      menu: createMenubar(calendarCardConfig),
    },
  };
};

const createBiCalendarCardPlugin = cardFactory(createPlugin);

export default createBiCalendarCardPlugin;
